<template>
  <div>
    <div>
      <section class="info_info">
        <div class="left">
          <div class="left_info">
            <strong>服务图片</strong>
            <a href="javascript:void(0)" @click="upload" v-if="images.length!==0">+添加图片</a>
          </div>
          <div class="left_photo">
            <div class="left_photo_one" v-if="images.length===0" @click="upload">
              <Icon type="images" size="50" color="#dddddd"></Icon>
              <h2 style="color: #dddddd;">上传图片</h2>
              <p style="color: #dddddd">最多可增加十张图片</p>
            </div>
            <div class="single-image-wrapper" v-else-if="images.length===1" @mouseover="shade">
              <div class="shade" v-if="shadeShow" @mouseout="cancleShade">
                <a href="javascript:void (0)" @click="upload">
                  <Icon type="loop" size="35" color="#f3f3f3" style="margin:0 0.25rem"></Icon>
                </a>
                <a href="javascript:void (0)" @click="deletePhoto">
                  <Icon type="android-delete" size="35" color="#f3f3f3"></Icon>
                </a>
              </div>
              <img :src="images[0].pic" alt="暂无图片信息" style="width: 100%;height: 100%">
            </div>
            <div class="images-wrapper" v-else>
              <div class="add_image">
                <div class="image-item" @mouseenter="imageItems(key)" @mouseleave="cancleImageItems(key)" v-for="(item,key) in images">
                  <div class="image-cover" v-if="item.show">
                    <Tooltip content="将图片设置成封面图片" placement="top">
                      <a href="javascript:void(0)" @click="setImage(key)">
                        <Icon type="image" size="25" color="#f3f3f3" v-if="key!==0"></Icon>
                      </a>
                    </Tooltip>
                    <a href="javascript:void (0)" @click="upload">
                      <Icon type="loop" size="25" color="#f3f3f3" style="margin:0 0.25rem"></Icon>
                    </a>
                    <a href="javascript:void (0)" @click="deletePhoto(key)">
                      <Icon type="android-delete" size="25" color="#f3f3f3"></Icon>
                    </a>
                  </div>
                  <img :src="item.pic" alt="" style="width:1.4rem;height: 100%">
                </div>
                <div class="image-item place-holder" @click="upload" style="width: 160px">+</div>
              </div>
            </div>
          </div>
        </div>
        
        
        
        
        <div class="add_right">
          <div style="display: flex;">
            <div style="margin-right: 20px;">
              <strong>服务名称</strong>
              <span style="color: #e64751;margin-bottom: -0.02rem;display: inline-block;margin-left: 1.2rem;">{{msg}}</span><br />
              <input type="text" v-model.trim="name" placeholder="服务名称" @blur="change" ref="input"  @input="input" />
            </div>
            <div style="position: relative;">
              <strong>价格</strong>
              <span style="color: #e64751;margin-bottom: -0.02rem;display: inline-block;margin-left: 1.75rem;">{{msggin}}</span><br />
              <span class="cny_span">CNY</span>
              <span class="cny_zi">/次</span>
              <input v-model.trim="price" ref="inputer" type="text" @input="inputer" @blur="changer" style="padding-left: 0.5rem;" />
            </div>
          </div>
            <div style="margin-top: 0.05rem">
              <strong>服务简介</strong>
              <span style="font-size: 0.14rem;color:#e64751;margin-left: 3.5rem;">{{textNum}}字以内</span>
            </div>
            <textarea v-model.trim="text" cols="30" rows="10" class="text" placeholder="简单描述下你的服务吧。 (选填)" ref="text" @focus="textF" @blur="change" @input="inputLength"></textarea>

        </div>
      </section>
    </div>
      
      <div class="left">
        


    <p style="margin-top: 0.2rem"><strong>分类</strong></p>
    <div class="sorts">
      <Select v-model="sorts" style="width:200px" v-if="sortsInfo.length!==0">
        <Option v-for="item in sortsInfo" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>
      <a href="JavaScript:void (0)" @click="addSort" v-if="sortsInfo.length===0">添加服务分类(可选)</a><span>可以使您的访客更方便的找到所需服务.</span>
      <Tooltip placement="right" content="管理服务分类请前往服务分类" class="Tooltip">
        <Icon type="help-circled" size="18"></Icon>
      </Tooltip>
    </div>
    </div>
    <div class="left">
    <p style="margin-top: 0.2rem;"><strong>详情描述</strong></p>
    <div class="info">
      <Checkbox v-model="content_show" false-value="0" true-value="1"><span style="font-size: 0.14rem">在服务详情页上显示更多详情</span></Checkbox>
    </div>
    <text-editor @inputInfo="goodsInfo" v-if="content_show==='1'"></text-editor>
    </div>
    <footer>
      <a href="javaScript:void(0)" @click="cancel">
        < 取消</a>
          <a href="javaScript:void(0)" @click="submit" v-if="!load">保存</a>
          <a href="javaScript:void(0)" v-if="load">保存 <img src="../../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></a>
    </footer>
    <modal ref="upload"></modal>
  </div>
</template>
<script>
  import modal from '../../upload/modal.vue'
  import textEditor from '../../textEditor.vue'
  export default {
    data() {
      return {
        load : false,
        shadeShow: false,
        //提示信息
        single: true,
        singleInfo: false,
        msg: "",
        msggin: "",
        text: '',
        textNum: 600,
        name: "",
        price: '',
        imgList: [],
        desc: '',
        sorts: '',
        content_show: 0,
        content: "",
        src: '',
      }
    },
    computed: {
      images() {
        return this.$store.getters.getImgLists;
      }
    },
    props: ['sortsInfo'],
    methods: {
      goodsInfo(val) {
        this.content = val;
      },

      setImage(key) {
        let src = this.images[0];
        this.images[0] = this.images[key];
        this.images.splice(key, 1, src)
      },
      //        多张图片显示
      imageItems(key) {
        this.images[key].show = true;
        this.images.splice(key, 1, this.images[key]);
      },
      //      多张图片隐藏
      cancleImageItems(key) {
        this.images[key].show = false;
        this.images.splice(key, 1, this.images[key]);
      },
      deletePhoto(key) {
        this.$store.commit('deleteImgLists', key);
      },
      shade() {
        this.shadeShow = true;
      },
      cancleShade() {
        this.shadeShow = false;
      },
      //       添加图片
      addPhoto() {
        this.$refs.upload.modal = true;
      },
      change1() {},
      //上传图片
      upload() {
        this.$refs.upload.modal = true;
        this.$store.dispatch('goodsPhoto', 1);
      },
      addSort() {
        this.$router.push('serveSorts')
      },
      inputLength() {
        //        this.textNum-=this.text.length;
      },
      //          聚焦
      textF() {
        this.$refs.text.style.borderColor = 'skyblue';
      },
      //      输入设置
      input() {
        this.$refs.input.style.borderColor = 'skyblue';
        this.msg = ''
      },
      //      检测
      change() {
        this.$refs.text.style.borderColor = 'gainsboro';
        if(this.name) {
          this.$refs.input.style.borderColor = 'gainsboro';
        } else {
          this.$refs.input.style.borderColor = '#e64751';
          this.msg = '服务名称不能为空'
        }
      },
      inputer() {
        this.$refs.inputer.style.borderColor = 'skyblue';
        this.msggin = ''
      },
      //      检测
      changer() {
        this.$refs.text.style.borderColor = 'gainsboro';
        if(this.name) {
          this.$refs.inputer.style.borderColor = 'gainsboro';
        } else {
          this.$refs.inputer.style.borderColor = '#e64751';
          this.msggin = '价格不能为空'
        }
      },

      control(key) {
        if(!this.lists[key].disable) {
          this.lists[key].disable = true;
          this.lists[key].lastNum = '∞';
        } else {
          this.lists[key].disable = false;
          this.lists[key].lastNum = '1';
        }
      },
//   删除服务
      close(key) {
        if(this.lists.length === 2) {
          this.lists.splice(key, 1);
          this.lists[0].show = false;
        } else if(this.lists.length === 1) {

        } else {
          this.lists.splice(key, 1);
        }
      },
      cancel() {
        this.$store.commit('setStatus', true);
        this.$parent.shown = true;
        this.$parent.show = false;
        this.$parent.changeShow = false;
      },
//    添加服务
      submit() {
        if(this.name) {
          this.load = true;
          let arr = [];
          arr = this.images.map(item => {
            return item = item.pic.slice(29)
          });
          this.post("custom/Service/createServiceItems", {
            service_name: this.name,
            service_price: this.price,
            service_pic: arr.toString(),
            service_desc: this.text,
            cate_id: this.sorts,
            is_particulars: this.content_show,
            service_particulars: JSON.stringify(this.content),
          }).then(res => {
            this.load = true;
            if(res.data.code === 10000) {
              const content = res.data.msg;
              this.$Modal.success({
                title: "添加成功",
                content: content
              });
              this.cancel();
//              更新数据
                this.post('custom/Service/getServiceItems', {
                  page: 1,
                  number: 15,
                }).then(res => {
                  if(res.data.code === 10000) {
                    let goodsLists = res.data.data.info;
                    goodsLists.forEach(item => {
                      item.pic = "http://weapp.xiguawenhua.com/" + item.service_pic.split(',')[0];
                      item["show"] = false
                    });
                    window.frames[0].postMessage(goodsLists,"*");
                    this.$emit("update",goodsLists);
                  }
                })
            } else {
              const content = res.data.msg;
              this.$Modal.error({
                title: "添加失败",
                content: content
              });
            }
          })
        }
      },
      changeGoods() {

      }
    },
    components: {
      modal,
      textEditor
    },
    watch: {
      text(newVal, oldVal) {
        this.textNum -= (newVal.length - oldVal.length)
      },
      sortsInfo(val) {
        return val;
      }
    },
    mounted() {

    },
  }
</script>
<style scoped>
  h1 {
    font-size: 0.16rem;
    font-weight: 600
  }

  .info {
    padding: 0.05rem 0;
    margin-bottom: 0.2rem;
  }

  .left_info {
    justify-content: space-between;
    align-items: center
  }

  .left_info>strong {
    line-height: 0.3rem;
    display: inline-block;
    font-size: 0.15rem;
  }

  .left_info>a {
    line-height: 0.2rem;
    display: inline-block;
    font-size: 0.12rem;
    background: #55A532;
    width: 0.8rem;
    text-align: center;
    color: whitesmoke;
    border-radius:0.02rem ;
  }

  .left_photo {
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 0.05rem;
    border: 1px solid #CCCCCC;
    background: #f6f6f6;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .left_photo_one {
    text-align: center;
  }

  .single-image-wrapper {
    width: 100%;
    height: 100%;
  }

  .shade {
    width: 1.4rem;
    height: 1.4rem;
    position: absolute;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .images-wrapper {
    height: 1.82rem;
    width: 100%;
  }

  .image-item {
    border: 1px solid #e2e4e7;
    background: #F6F6F6;
    display: inline-block;
    margin-bottom: 20px;
    margin-right: 20px;
    position: relative;
    vertical-align: top;
    margin-top: 0.2rem;
    width: 1.4rem;
    height: 1.4rem;
  }
  .place-holder {
    border: 2px dashed #ccc;
    box-sizing: border-box;
    color: #ccc;
    cursor: pointer;
    font-size: 50px;
    line-height: 1rem;
    text-align: center;
  }

  .image-cover {
    background: rgba(0, 0, 0, .5);
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .add_right{
    padding-top: 0.1rem;
    margin-top: 0.2rem;
    padding-left: 0.2rem;
    background: white;
    padding-bottom: 0.2rem;
    box-shadow: 5px 5px 10px #E7E7E7;
   
  }
  .add_right strong {
    display: inline-block;
    font-size: 0.15rem;
    padding: 0.05rem 0
  }

  .add_right input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.1rem 0.1rem;
  }

  .add_right .text {
    width: 500px;
    padding: 0.1rem 0.1rem;
    height: 2.05rem;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    display: inline-block;
  }
  /*.left>div{height: 30vh;}*/

  .price p {
    padding: 0.1rem 0
  }

  p>strong {
    font-size: 0.16rem;
    ;
    margin-top: 0.1rem
  }

  .price {
    display: flex;
    margin-bottom: 0.15rem
  }

  .left input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.1rem 0.1rem;
  }

  .sorts {
    padding: 0.05rem 0;
    font-size: 0.14rem
  }

  .delivery {
    padding: 0.05rem 0;
    font-size: 0.14rem
  }

  footer {
    position: fixed;
    bottom: 0;
    padding: 0.2rem 0.3rem;
    margin-left: -0.2rem;
    background: #fff;
    box-shadow: 0 -2px 3px #ddd;
    box-sizing: border-box;
    width: calc(100vw - 2.6rem);
    text-align: center;
  }

  footer>a {
    color: black;
    display: inline-block;
    padding: 0.1rem 0.5rem;
    background: #eee;
    border-radius: 0.05rem;
  }

  footer>a:nth-child(1) {
    float: left;
  }

  footer>a:nth-child(2) {
    color: white;
    background: #93b719;
  }

  .cny_span {
    position: absolute;
    font-size: 0.12rem;
    top: 0.435rem;
    left: 0.15rem;
    display: inline-block;
    width: 0.3rem;
    height: 0.15rem;
    line-height: 0.15rem;
    text-align: center;
    color: #ACB2B4;
    border: 1px solid #A9AEB2;
    border-radius: 0.04rem;
  }

  .cny_zi {
    position: absolute;
    top: 0.435rem;
    left: 2.7rem;
  }
  .add_image{
    display: inline-flex;
  }
  
  .left{
    padding-top: 0.1rem;
     margin-top: 0.2rem;
    padding-left: 0.2rem;
    background: white;
    padding-bottom: 0.2rem;
    box-shadow: 5px 5px 10px #E7E7E7;
  }
</style>



