    /*margin-top:20px;*/
    /*overflow: hidden;*/
  /*}*/
  /*h1 {*/
    /*font-size: 0.16rem;*/
    /*font-weight: 600*/
  /*}*/

  /*.info {*/
    /*padding: 0.05rem 0;*/
    /*margin-bottom: 0.2rem;*/
  /*}*/

  .left_info {
    justify-content: space-between;
    align-items: center
  }

  .left_info>strong {
    line-height: 0.3rem;
    display: inline-block;
    font-size: 0.15rem;
  }

  .left_info>a {
    line-height: 0.2rem;
    display: inline-block;
    background: #59A2ED;
    width: 0.8rem;
    text-align: center;
    color: whitesmoke;
    font-size: 12px;
  }

  .left_photo {
    width: 1.4rem;
    height: 1.4rem;
    /*border: 1px solid #CCCCCC;*/
    /*background: #f6f6f6;*/
    border:none;
    background: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  /*.left_photo_one {*/
    /*text-align: center;*/
  /*}*/

  /*.single-image-wrapper {*/
    /*width: 100%;*/
    /*height: 100%;*/
  /*}*/

  .shade {
    width: 1.4rem;
    height: 1.4rem;
    position: absolute;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .images-wrapper {
    height: 1.82rem;
    width: 100%;
    overflow: inherit;
  }

  .image-item {
    border: 1px solid #e2e4e7;
    background: #F6F6F6;
    display: inline-block;
    margin-bottom: 20px;
    margin-right: 20px;
    position: relative;
    vertical-align: top;
    margin-top: 0.2rem;
    width: 1.4rem;
    height: 1.4rem;
  }
  .place-holder {
    border: 2px dashed #ccc;
    box-sizing: border-box;
    color: #ccc;
    cursor: pointer;
    font-size: 50px;
    line-height: 1.3rem;
    text-align: center;
  }

  /*.image-cover {*/
    /*background: rgba(0, 0, 0, .5);*/
    /*height: 100%;*/
    /*left: 0;*/
    /*position: absolute;*/
    /*top: 0;*/
    /*width: 100%;*/
    /*z-index: 10;*/
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
  /*}*/
  .right{
    /*margin-top: 0.2rem;*/
  }
  /*.right strong {*/
    /*display: inline-block;*/
    /*font-size: 0.15rem;*/
    /*padding: 0.05rem 0*/
  /*}*/

  .right input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.1rem 0.1rem;
  }

  /*.right .text {*/
    /*width: 500px;*/
    /*padding: 0.1rem 0.1rem;*/
    /*height: 2.05rem;*/
    /*border-radius: 0.05rem;*/
    /*border: 1px solid gainsboro;*/
    /*display: inline-block;*/
  /*}*/
  /*.left>div{height: 30vh;}*/

  /*.price p {*/
    /*padding: 0.1rem 0*/
  /*}*/

  /*p>strong {*/
    /*font-size: 0.16rem;*/
    /*margin-top: 0.1rem;*/
  /*}*/

  /*.price {*/
    /*display: flex;*/
    /*margin-bottom: 0.15rem*/
  /*}*/

  /*.left input {*/
    /*display: inline-block;*/
    /*width: 300px;*/
    /*border-radius: 0.05rem;*/
    /*border: 1px solid gainsboro;*/
    /*padding: 0.1rem 0.1rem;*/
  /*}*/

  /*.sorts {*/
    /*padding: 0.05rem 0;*/
    /*font-size: 0.14rem*/
  /*}*/

  /*.delivery {*/
    /*padding: 0.05rem 0;*/
    /*font-size: 0.14rem*/
  /*}*/

  footer {
    position: fixed;
    bottom: 0;
    padding: 0.2rem 0.3rem;
    margin-left: -0.2rem;
    background: #fff;
    box-shadow: 0 -2px 3px #ddd;
    box-sizing: border-box;
    width: calc(100vw - 2.6rem);
    text-align: center;
  }

  footer>a {
    color: black;
    display: inline-block;
    padding: 0.1rem 0.5rem;
    background: #eee;
    border-radius: 0.05rem;
  }

  footer>a:nth-child(1) {
    float: left;
  }

  footer>a:nth-child(2) {
    color: white;
    background: #59A2ED;
  }

  .cny_span {
    position: absolute;
    font-size: 0.12rem;
    top: 0.435rem;
    left: 0.15rem;
    display: inline-block;
    width: 0.3rem;
    height: 0.15rem;
    line-height: 0.15rem;
    text-align: center;
    color: #ACB2B4;
    border: 1px solid #A9AEB2;
    border-radius: 0.04rem;
  }

  .cny_zi {
    position: absolute;
    top: 0.435rem;
    left: 2.7rem;
  }
  .add_image{
    display: inline-flex;
  }
</style>



