<template>
  <div class="goods">
    <topServer></topServer>
    <div class="content">
      <div v-show="shown">
        <div class="addCenter" v-if="goodsLists.length == 0">
          <div class="addNewServer">
            <a href="JavaScript:void(0)" @click="add">
              <i class="iconfont icon-iconjia"></i>
              <span>添加新服务</span>
            </a>
          </div>
          <div class="noServer_addServer">
            <div class="no_add_div">
              <img src="../../../assets/icon-service-empty.png"/>
              <p>暂未添加服务</p>
              <p>添加服务后，顾客可进行预约</p>
            </div>
          </div>
        </div>
        <div class="add_server" v-else>
          <div style="display: flex;justify-content: space-between">
            <div class="addNewServer">
              <a href="JavaScript:void(0)" @click="add">
                <i class="iconfont icon-iconjia"></i>
                <span>添加新服务</span>
              </a>
            </div>
            <div>
              <Select v-model="type" style="width:200px" @on-change="selectType">
                <Option v-for="item in list" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </div>
          </div>
          <p v-if="noGoods">还没有添加服务</p>
          <div class="all">
            <div v-for="(item,index) in goodsLists" @mouseenter="enter(index)" @mouseleave="out(index)">
              <div class="goods_zhe" v-if="item.show">
                <div class="close">
                  <Icon type="ios-close" size="25" @click.native="close(index,item)" color="#969696"></Icon>
                </div>
                <p class="goods_center"><span @click="editor(index,item)">编辑服务</span></p>
              </div>
              <div class="goods_one">
                <img :src="item.pic" alt="">
                <div class="foot">
                  <p>{{item.service_name}}</p>
                  <p>{{item.service_price}}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <transition name="slide-fade">
        <addServe v-if="show" :sortsInfo="info" @update="updateData"></addServe>
      </transition>
      <transition name="slide-fade">
        <changeInfo v-if="changeShow" :sortsInfo="info"></changeInfo>
      </transition>
    </div>
  </div>
</template>
<script>
import topServer from '../topServer.vue'
import addServe from './addServe.vue'
import changeInfo from './changeServe.vue'
  export default {
    data() {
      return {
        type:0,
        noGoods: false,
        Sshown:false,
        shutShow:true,
        info: [],
        goodsLists: [],
        list: [{
            value: 1,
            label: '服务分类(0)'
          },
          {
            value: 2,
            label: '没有分类(0)'
          }
        ],
        shown: true,
        show: false,
        changeShow: false,
        src:[]
      }
    },
    methods: {
      updateData(val){
          this.goodsLists=val;
      },
//        获取分类列表
      getSelectType(type){
        this.post('custom/Service/getServiceItems', {
          page: 1,
          number: 15,
          cate_id:type
        }).then(res => {
          if(res.data.code === 10000) {
            this.goodsLists = res.data.data.info;
            this.goodsLists.forEach(item => {
              item.pic = "http://weapp.xiguawenhua.com/" + item.service_pic.split(',')[0];
              item["show"] = false
            });
            window.frames[0].postMessage(this.goodsLists,"*");
          }else{
            this.$Modal.error({
              title: "筛选失败",
              content: res.data.msg
            });
          }
        })
      },
//        筛选
      selectType(){
        this.getSelectType(this.type);
      },
//    鼠标滑过事件
      out(index) {
        this.goodsLists[index].show = false;
        this.goodsLists.splice(index, 1, this.goodsLists[index]);
      },
//    删除服务
      close(index, item) {
        this.$Modal.confirm({
          title: '删除服务',
          content: '<p>确认要删除该服务吗?</p>',
          onOk: () => {
            this.post("custom/Service/delServiceItems", {
              service_id: item.id,
            }).then(res => {
              if(res.data.code === 10000) {
                const content = res.data.msg;
                this.$Modal.error({
                  title: "删除成功",
                  content: content
                });
                location.reload();
              }
            })
          },
          onCancel: () => {

          }
        });
      },
      //      编辑某个商品
      editor(index, item) {
        this.$store.commit('setStatus', false);
        this.shown = false;
        this.show = false;
        this.changeShow = true;
        sessionStorage.setItem("id", item.id);
      },
//    鼠标移入事件
      enter(index) {
        this.goodsLists[index].show = true;
        this.goodsLists.splice(index, 1, this.goodsLists[index]);
      },
      add() {
        this.$store.commit('setStatus', false);
        this.$store.dispatch('imgLists', []);
        this.show = true;
        this.shown = false;
//      this.shutShow=false;
        this.changeShow = false;
      }
    },
    mounted() {
      //         服务列表
      this.getSelectType();
    },
    created() {
      this.post('custom/Cate/sublistCate', {}).then(res => {
        if(res.data.code === 10000) {
          if(res.data.data.info.length === 0) {

          } else {
            this.list = res.data.data.info;
            this.list.forEach((item, index) => {
              item['value'] = item.id;
              item['label'] = item.name;
            });
            this.info = this.list;
          }
        }
      })
    },
    activated(){

    },
    components: {
      topServer,
      addServe,
      changeInfo
    }
  }
</script>
<style scoped>
  /*@import url("global.css");*/
.goods {
    position: relative;
  }

  .content {
    height: calc(100vh - 78px - 92px);
    padding: 0.2rem 0;
    overflow-y: auto
  }
  .content::-webkit-scrollbar{
    width:0.05rem;
    height:0.1rem;
  }
  .content::-webkit-scrollbar-thumb {
    border-radius: 0.1rem;
    background-color: rgba(0, 0, 0, 0.2);
  } /* 滑块颜色 */
  .goods_add a {
    display: inline-block;
    padding: 0.1rem 0.2rem;
    background: #59A2ED;
    color: white;
    font-size: 0.14rem;
    border-radius: 0.03rem
  }

  .goods_add>span {
    display: inline-block;
    font-size: 0.14rem;
    padding: 0.1rem;
  }

  .goods_add>p {
    padding: 0.2rem 0;
    font-size: 0.13rem
  }

  .slide-fade-enter-active {
    transition: all 1s ease;
  }

  .slide-fade-enter {
    transform: translateX(70%);
  }

  .all {
    display: flex;
    flex-wrap: wrap;
    margin-top: 0.1rem;
  }

  .goods_one {
    width: 1.66rem;
    height: 2.2rem;
    border-radius: 0.03rem;
    border: 1px solid #eeeeee;
    cursor: pointer;
    text-align: center;
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
  }

  .goods_one>img {
    height: 1.7rem;
    width: 1.6rem;
  }

  .foot {
    text-align: left;
    color: #111111;
    margin: 0.05rem 0.15rem;
    padding-bottom: 0.2rem;
  }

  .goods_zhe {
    position: absolute;
    width: 1.66rem;
    height: 2.2rem;
    border: 1px solid #f3f3f3;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
    background-color: rgba(246, 246, 246, 0.5);
  }

  .all>div {
    width: 1.66rem;
    height: 2.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .close {
    align-self: flex-end;
    margin-top: 0.2rem;
    margin-right: 0.2rem;
  }

  .goods_center {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .goods_center>span {
    color: white;
    display: inline-block;
    padding: 0.05rem 0.1rem;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0.8;
  }
  .content_tishi{
    color:#606060;
    letter-spacing: 0.02rem;
    margin: 0.1rem 0.02rem;
    display: inline-block;
  }
  /*没有新服务添加新服务*/
  .noServer_addServer{
    display:flex;
    justify-content: center;
    margin-top: 112px;
  }
  .no_add_div{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 2rem;
    flex-direction: column;
  }
  .no_add_div>img{
    height: 1.7rem;
    width: 1.6rem;
  }
  .no_add_div>p{
    display: flex;
    justify-content: center;
    color: #BCBBBB;
    font-size: 14px;
  }

  /*添加新服务*/
  .addNewServer>a{
    width:111px;
    height:36px;
    background-color: #59A2ED;
    color: #fff;
    display: inline-flex;
    justify-content: space-between;
    padding:0 12px;
    box-sizing: border-box;
    border-radius: 3px;
  }
  .addNewServer>a>span,.addNewServer>a>i{
    display: flex;
    align-items: center;
    font-size: 13px;
  }
</style>



